<template>
  <div>
    <md-checkbox v-for="item in response.reponses" v-model="array" :value="item">{{ item }}</md-checkbox>
    <br>
    <md-button class="md-raised md-primary" v-on:click="$emit('nextQuestion', checkResponse())">Suivante →</md-button>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'CB_ReponseOuverte',
  props: ['response'],
  data () {
    return {
      array: [],
      inputAnswer: null
    }
  },
  methods: {
    checkResponse: function () {
      var res = true
      if (this.response.correcte.length != this.array.length)
        res = false
      for (var i in this.array)
        if (this.response.correcte.indexOf(this.array[i]) === -1)
          res = false
      this.array = []
      return res
    }
  },
  created: function () {},
  mounted: function () {},
  watch: {}
}
</script>

<style scoped>

</style>



