<template>
  <div>
    <md-field class="md_field">
      <label>Votre Réponse</label>
      <md-input v-model="inputAnswer"></md-input>
    </md-field>
    <md-button class="md-raised md-primary" v-on:click="$emit('nextQuestion', checkResponse())">Suivante →</md-button>
  </div>
</template>

<script>
export default {
  name: 'CB_ReponseOuverte',
  props: ['response'],
  data () {
    return {
      inputAnswer: ''
    }
  },
  methods: {
    checkResponse: function () {
      return this.inputAnswer.toLowerCase().trim() === this.response.correcte.toLowerCase().trim()
    }
  },
  created: function () {},
  mounted: function () {},
  watch: {}
}
</script>

<style scoped>
.md_field{
  margin: 0 auto;
  width: 30%;
  min-width: 250px;
}
</style>



