<template>
  <div>
    <!-- $root.cb_personne.cb_nom -->
    <!-- <button v-on:click="counter += 1">Question Suivante</button> {{ counter }} -->
    <p> {{ questions[counter].question.text }} </p>
    <CB_ReponseOuverte v-if="questions[counter].question.question_type=='ouverte'" :response="questions[counter].reponse" @nextQuestion="getResponse"/>
    <CB_ReponseQCM v-if="questions[counter].question.question_type=='qcm'" :response="questions[counter].reponse" @nextQuestion="getResponse"/>
  </div>
</template>

<script>
/* eslint-disable */
import jsonQuestions from './../../ressource/questions.json'
// eslint-disable-next-line
import CB_ReponseOuverte from './CB_ReponseOuverte.vue'
// eslint-disable-next-line
import CB_ReponseQCM from './CB_ReponseQCM.vue'
export default {
  name: 'CB_Question',
  components: {
    CB_ReponseOuverte, CB_ReponseQCM
  },
  props: [],
  data () {
    return {
      //cb_nom_register: this.$root.cb_personne.cb_nom,
      counter: 0,
      questions: jsonQuestions.list_question,
      correcteAnswer: 0
    }
  },
  methods: {
    counterBtn: function () {
      this.counter++
    },
    getResponse: function (d) {
      if(d)
        correcteAnswer++
      //else
      this.counter++
    }
  },
  created: function () {
    if (this.$root.cb_personne.cb_nom == null || this.$root.cb_personne.cb_prenom == null || this.$root.cb_personne.cb_entreprise == null)
      this.$router.push('/')
  },
  mounted: function () {},
  watch: {
    counter: function (val) {
      if (this.counter == questions.length - 1)
        router.push('/result')
    },
  }
}
</script>

<style scoped>

</style>



